<template>
    <div>
        <!-- 雷达图按钮 -->
        <button @click="showRadarChart" class="custom-btn">查看我的笔试报告</button>
        <button @click="tomianshi" class="custom-btn">已经查看完笔试报告，前去面试</button>

        <!-- 雷达图模态窗口（支持滚动） -->
        <div v-if="showModal" class="modal">
            <div class="modal-content"> <!-- 新增滚动样式 -->
                <button @click="closeModal" class="close-btn">×</button>
                <h2>笔试能力评估</h2>
                <canvas ref="radarChart"></canvas>
                <div class="ability-details">
                    <h3>能力详情</h3>
                    <ul>
                        <li v-for="(indicator, index) in indicators" :key="index">
                            {{ indicator }}:
                            <span :class="values[index] === 100 ? 'correct' : 'incorrect'">
                                {{ values[index] === 100 ? '优秀' : '待提高' }}
                            </span>
                        </li>
                    </ul>
                </div>
                <div class="ai-evaluation">
                    <h3>AI面试官评价</h3>
                    <p>{{ aiEvaluation }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Chart, registerables, RadarController, PolarAreaController } from 'chart.js';

export default {
    data() {
        return {
            showModal: false,
            indicators: [], // 5个人工智能专业术语
            values: [],     // 0-100分（对=100，错=0）
            aiEvaluation: '',
            chart: null
        };
    },
    mounted() {
        Chart.register(...registerables);
        Chart.register(RadarController, PolarAreaController);
        window.Chart = Chart;
    },
    methods: {
        tomianshi(){
            this.$router.push('/mianshi'); 
        },
        async showRadarChart() {
            try {
                const question = localStorage.getItem('question');
                const answer = localStorage.getItem('answer');

                if (!question || !answer) {
                    alert('请先完成笔试并获取评估结果');
                    return;
                }

                // 🔥 要求提取5个AI专业术语，数值0-100
                const prompt = `
          题目：${question}
          答案：${answer}
          
          任务：
          1. 从题目中提取5个人工智能专业术语（如CNN、RNN、PCA、Transformer、注意力机制）；
          2. 生成雷达图数据：
             - indicators：["术语1", "术语2", "术语3", "术语4", "术语5"]
             - values：答案正确为100，错误为0；
          3. 格式严格：
             indicators = ["Term1", "Term2", "Term3", "Term4", "Term5"]
             values = [100, 0, 100, 0, 100]
        `;

                const response = await fetch('https://117.72.49.76:443/post_answer', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ user_id: 'test_user', question: prompt })
                });

                const data = await response.json();
                const chatText = data.data || '';

                this.parseRadarData(chatText);
                this.aiEvaluation = await this.generateAIEvaluation();

                this.showModal = true;
                await this.$nextTick(); // 等待DOM更新
                this.renderRadarChart();
            } catch (error) {
                console.error('生成雷达图失败:', error);
                alert('数据获取失败，请重试');
            }
        },

        parseRadarData(chatText) {
            // 提取5个术语和对应分数
            const indicatorsMatch = chatText.match(/indicators\s*=\s*\[([^\]]+)\]/i);
            const valuesMatch = chatText.match(/values\s*=\s*\[([^\]]+)\]/i);

            // 清洗术语（去引号、空格）
            this.indicators = (indicatorsMatch?.[1] || '')
                .split(',')
                .map(term => term.trim().replace(/["']/g, ''));

            // 确保分数是0或100
            this.values = (valuesMatch?.[1] || '')
                .split(',')
                .map(val => {
                    const num = parseFloat(val.trim());
                    return num === 1 ? 100 : 0; // 兼容大模型可能返回的1/0
                });
        },

        renderRadarChart() {
            const ctx = this.$refs.radarChart.getContext('2d');
            if (this.chart) this.chart.destroy();

            this.chart = new Chart(ctx, {
                type: 'radar',
                data: {
                    labels: this.indicators,
                    datasets: [{
                        label: '能力评估',
                        data: this.values,
                        borderColor: '#4CAF50',
                        backgroundColor: 'rgba(76, 175, 80, 0.2)',
                        pointRadius: 5
                    }]
                },
                options: {
                    scale: {
                        ticks: {
                            beginAtZero: true,
                            max: 100,      // 评分范围0-100
                            stepSize: 20,  // 刻度间隔20
                            callback: (value) => `${value}分` // 显示"80分"
                        }
                    },
                    legend: { position: 'top' },
                    layout: { padding: 20 }
                }
            });
        },

        async generateAIEvaluation() {
            // 基于5个维度生成评价
            const prompt = `
        作为AI领域面试官，根据以下5个维度的表现（100=正确，0=错误）：
        ${this.indicators.map((term, i) => `
        - ${term}: ${this.values[i] === 100 ? '正确' : '错误'}
        `).join('')}
        
        请输出专业评价和改进建议（100-200字）。
      `;

            const response = await fetch('https://117.72.49.76:443/post_answer', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ user_id: 'test_user', question: prompt })
            });
            return (await response.json()).data || '暂无评价';
        },

        closeModal() {
            this.showModal = false;
            if (this.chart) this.chart.destroy();
        }
    },
    beforeDestroy() {
        if (this.chart) this.chart.destroy();
    }
};
</script>

<style scoped>
/* 模态框支持滚动 */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 12px;
    width: 80%;
    max-width: 600px;
    position: relative;
    max-height: 80vh;
    /* 限制高度，超出滚动 */
    overflow-y: auto;
    /* 滚动条 */
}

.close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.2em;
    cursor: pointer;
}

.ability-details {
    margin: 15px 0;
    padding: 15px;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
}

.ai-evaluation {
    margin-top: 20px;
    padding: 15px;
    background: #f8f9fa;
    border-radius: 8px;
}

.correct {
    color: #2ecc71;
}

.incorrect {
    color: #e74c3c;
}
</style>